@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.editor {
  font-family: 'jetbrains-mono', monospace;
  counter-reset: line;
  border: 1px solid #ced4da;
}

.editor #code-area {
  outline: none;
  padding-left: 60px !important;
}

.editor pre {
  padding-left: 60px !important;
}

.editor pre .token {
  font-family: 'jetbrains-mono', monospace !important;
}

.editor pre .punctuation {
  color: black;
  font-weight: bold;
}

.editor pre .data-type {
  color: #990055;
  font-weight: bold;
}

.editor .editor-line-number {
  font-family: 'jetbrains-mono', monospace;
  position: absolute;
  left: 0;
  color: #a1a1a1;
  text-align: right;
  width: 40px;
  font-weight: 100;
  border-right: 1px solid #e3e9ef;
  padding-right: 15px;
  background-color: #efefef;
}

code[class*="language-"], pre[class*="language-"] {
  font-family: 'jetbrains-mono', monospace;
}
.errors-list-element {
    color: darkred;
}
.errors-wrapper {
    margin-top: 1rem;
}

.no-errors {
    display: flex;
    color: darkgreen;
    margin-top: 1rem;
    align-items: center;
}

.no-errors-str {
    margin-left: 1rem;
}

.found-errors {
    display: flex;
    color: darkred;
    align-items: center;
    margin-bottom: 0.5rem;
}

.found-errors-str {
    margin-left: 1rem;
}

.errors-list {
    list-style: none;
    padding: 0;
    margin: 0;
}
.examples-wrapper {
  margin-top: 2rem;
}

.examples-wrapper .examples-header {
  font-weight: bold;
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.examples-wrapper .examples-search {
  padding: 0.45rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  width: calc(100% - 1.5rem - 2px);
  color: #3d566e;
  background-color: #fff;
  background-image: none;
  background-clip: padding-box;
  border: 1px solid #e1e3e6;
  border-radius: 4px;
  margin-bottom: 0.5rem;
}

.examples-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.example {
  display: flex;
  cursor: pointer;
  flex-direction: column;
  border-radius: 0.25rem;
  border: 0.025rem solid #464646;
  margin: 0.25rem 0;
  overflow: hidden;
  -webkit-user-select: none;
  user-select: none;
}

.example .example-header {
  background-color: #fff;
  color: #2d2d2d;
  padding: 0.5rem 0.75rem;
  font-weight: bold;
}
.example-code {
  position: relative;
  font-family: 'jetbrains-mono', monospace;
  font-size: 0.75rem !important;
  margin: 0.5rem 0;
  background-color: #fff;
  opacity: 0.75;
  border-top: 1px solid #e3e9ef;
  border-bottom: 1px solid #e3e9ef;
}

.example-code .example-code-line {
  padding-left: 60px;
}

.example-code .line-number {
  position: absolute;
  left: 0;
  color: #a1a1a1;
  text-align: right;
  width: 40px;
  font-weight: 100;
  border-right: 1px solid #e3e9ef;
  padding-right: 15px;
  background-color: #f6f6f6;
}

.example-code .example-code-line .token {
  font-family: 'jetbrains-mono', monospace !important;
}

.example-code .example-code-line .punctuation {
  color: black;
  font-weight: bold;
}

.example-code .example-code-line .data-type {
  color: #990055;
  font-weight: bold;
}
* {
  font-family: 'roboto', sans-serif;
}

.App {
  width: 900px;
  margin: auto;
}

#result {
  display: block;
  padding: 0.5rem;
}
